:root{
  
 --primary-1:  rgb(37, 167, 207);
 --primary-2:  rgb(25, 122, 151);
 --primary-3:  rgb(80, 180, 193);
 --secondary-1: rgb(10, 22, 26);
 --secondary-2: rgb(9, 32, 41);
 --secondary-3: #122d31;
 --dark-1: #799295;
 
  
}



*{
    box-sizing: border-box;
    padding: 0;
    margin: 0;
    /* scroll-behavior: smooth; */

}
html{
    scroll-behavior: smooth;
}
body{
    box-sizing: border-box;
    margin: 0;
    padding: 0;
font-family: 'Roboto', 'sans-serif';


}
.app{
    width: 100%;
    min-height: 100vh;
    background:var(--secondary-1);

}
.app_main{
    position: relative;
max-width: 1400px;
width: 100%;
margin: 0 auto;
z-index: 2;
}

#home{
    position: relative;
width:100%;
overflow:hidden;

}
#home::after{
    position: absolute;
    content: "";
    width: 100%;
    height: 9rem;
    bottom: 0;
    left:0;
    background: var(--secondary-2);
    transform: rotate(15deg);
    z-index: -2;
    border-radius: 2rem;
}